<script>
    import { afterUpdate } from 'svelte';
    import _ from 'lodash';

    import { titles } from '@store/stores';
    import { URL_APPEND } from '@general/constants';
    import Input from '@general/myInput.svelte';
    import { postFormData, postData } from '@general/myAxios';

    import Entry from './entry.svelte';

    // import {
    //     Button,
    //     Modal,
    //     ModalBody,
    //     ModalFooter,
    //     ModalHeader
    // } from 'sveltestrap';

    let tl_title = '', cover_card, tl_title_err='', isloading=false
    let itemsData = {}, items = [], files = [];

    let addItem = () => {
        let ids = items.length == 0 ? [0] : items.map(o => o.id)
        items = [...items, { id: Math.max(...ids) + 1 }]
    }

    let deleteItem = (key) => {
        items = items.filter(k => k.id !== key)
        delete itemsData[key]
    }

    let updateItemsData = (key, file = null, obj) => {
        if (itemsData[key] == undefined)
            itemsData[key] = obj
        else
            itemsData = { ...itemsData, [key]: obj }
        // Object.assign(itemsData[key], obj)

        if (file !== null && file.image !== null)
            files = [...files, file]

        files = _.uniqBy(files, 'name')
    }

    // $: console.log(itemsData)

    let saveItems = async () => {
        if (tl_title == '') {
            tl_title_err = 'Please add a timeline title';
            return;
        }else tl_title_err = '';

        items.map(itm => {
            isloading = document.getElementById('form' + itm.id).dispatchEvent(new Event("submit", { cancelable: true }))
        })

        setTimeout(function () {
           
            if (items.length == 0) return;
            if (items.length !== Object.values(itemsData).length) return;

            let mdl = {
                files,
                title: tl_title,
                data: Object.values(itemsData)
            }
            // console.log(mdl)

            postData('/create', mdl).then(res => {
                // console.log('saved');
                // window.location.href = location.origin + '/timeline'
                window.location.href = location.origin + '/highlights/timeline'
            })

            // postData('/upload', files).then(res => {
            //     console.log('saved');
            // });

        }, 1500);

    }

    let open = false
    const toggle = () => (open = !open);
</script>

<style>
    .btn2 {
        background-color: transparent;
        border: 0;
        cursor: pointer;
        font-size: 100px;
        font-weight: bold;
        width: 100%;
        height: 100%;
    }

    .error {
        color: red;
    }
</style>

<div class='row'>
    <div class='col-md-12'>
        <center>
            <div style="display: flex; flex-direction: row; width: 400px; height: 38px;">

                <!-- <Input type='dropdown' bind:selected={tl_title} placeholder="Select Timeline..." options={$titles.map(t=>({ value:
        t, text: t }))} /> -->
                <div>
                    <Input bind:nameval={tl_title} placeholder="* Timeline title" />
                    {#if tl_title_err !== ''}
                    <small class="error">{tl_title_err}</small>
                    {/if}
                </div>
                &nbsp;
                <button class="btn btn-danger" on:click={saveItems} disabled={isloading}>
                    {#if isloading}
                    Loading...
                    {:else}
                    Save
                    {/if}
                </button>

            </div>
        </center>
    </div>
</div>


<!-- {cover_card} -->
<div class='row' style="margin-top: 15px;">
    {#each items as item (item.id)}
    <div class='col-md-4'>
        <Entry key={item.id} {updateItemsData} {deleteItem}>
            <!-- <label>
                <input type=radio bind:group={cover_card} value={item.id}>
                Cover Page
            </label> -->
        </Entry>
    </div>
    {/each}

    <div class="col-md-4">
        <div class='card ' style="margin: 5px; height: 600px; min-height: 600px;  position: relative;">
            <div class='card-body align-items-center d-flex justify-content-center'>
                <button on:click={addItem} class="btn2">
                    +
                </button>
            </div>
        </div>
    </div>


</div>